import React, { useState, useEffect } from 'react'
import TokenDescription from './TokenDescription'
import useLocale from 'utils/hooks/useLocale'
import swal from 'sweetalert';
import { Dropdown } from 'components/ui'
import DropdownItem from 'components/ui/Dropdown/DropdownItem';
import { useSelector } from 'react-redux'
// import contractABI from './abi/contractABI.js'
import './SwitchButton.css'
import { ethers } from 'ethers'
import { text } from 'd3-fetch';

const defaultRouter = "0x10ED43C718714eb63d5aA57B78B54704E256024E";
const contractAddress = "0xa06058A579165F14e3027643Cf2C7040b5Aaf340";


const ControlPanel = () => {
    let language = useLocale();
    const account = useSelector(state => state.theme.defaultAccount);

    const [isPro, setIsPro] = useState(false);

    const [killNum, setKillNum] = useState(0);

    const texts = {
        title: language === "CN" ? "控制台" : "Control Panel",
        giveUpAlert: language === "CN" ? "注意：當所有權放棄後，無法再使用任何開關" : "Note: After you renounce the ownership, you'll never ever be able to control the contract"
    }

    const controlBoxStyle1 = {
        display: 'flex',
        height: '40px',
        width: '45%',
        border: '1px solid black',
        borderRadius: '20px',
        color: 'black',
        justifyContent: 'center',
        alignItems: 'center',
        marginTop: '15px',
    }
    const controlBoxStyle2 = {
        display: 'flex',
        height: '40px',
        width: '100%',
        border: '0.5px solid gray',
        borderRadius: '5px',
        color: 'black',
        justifyContent: 'center',
        alignItems: 'center',
        marginTop: '15px',
        textAlign: 'center',
        marginBottom: '15px',
        marginLeft: '10%',
        cursor: 'pointer',
    }

    const controlWrapperStyle = {
        display: 'flex',
        flexDirection: 'column',
        minHeight: '100px',
        width: '45%',
        minWidth: '300px',
        // border: '1px solid black',
        marginTop: '30px',
        borderRadius: '10px',
        // paddingRight: '5vw',
        padding: '4vw'
    }

    const titleStyle = {
        color: 'red',
        display: 'flex',
        textAlign: 'left',
        paddingLeft: '10px',
        top: '10px',
        left: '20px',
        fontSize: '30px',
        fontWeight: 'bold',
    }

    const inputStyle = {
        display: 'flex',
        height: '40px',
        width: '100%',
        border: '0.5px solid gray',
        borderRadius: '5px',
        color: 'black',
        justifyContent: 'center',
        alignItems: 'center',
        marginTop: '15px',
        textAlign: 'center',
        marginBottom: '15px',
        marginLeft: '10%',
    }

    const controls = [
        {
            title: language === "CN" ? "交易控制" : "Transaction",
            titleStyle: titleStyle,
            style: controlWrapperStyle,
            content:
                [
                    {
                        title: language === "CN" ? "開盤" : "Start Trading",
                        style: controlBoxStyle2,
                        description: "按下去即可開盤，無法關閉交易",
                    },
                    {
                        title: language === "CN" ? "設置殺區塊數" : "Kill Blocks",
                        style: controlBoxStyle2,
                        inputType: "number",
                        inputStyle: inputStyle,
                        description: "設置拉黑的區塊數量，用以防範機器人開盤購買",
                        function: e => setKillNum(e.target.value),
                    },
                ]
        },
        {
            title: language === "CN" ? "數量控制" : "Amount",
            titleStyle: titleStyle,
            style: controlWrapperStyle,
            content:
                [
                    {
                        inputType: "text",
                        inputStyle: inputStyle,
                        description: "請在上方輸入欲限購之數量"
                    },
                    {
                        title: language === "CN" ? "設置單筆限購" : "Set Max Tx Amount",
                        style: controlBoxStyle2,
                        // inputType: "number",
                        // inputStyle: inputStyle,
                        description: "單次買賣的數量上限",
                    },
                    {
                        title: language === "CN" ? "設置錢包限購" : "Set Max Wallet Amount",
                        style: controlBoxStyle2,
                        // inputType: "number",
                        // inputStyle: inputStyle,
                        description: "錢包持有的的數量上限",
                    },
                    {
                        title: language === "CN" ? "取消限購" : "Cancel Limit",
                        style: controlBoxStyle2,
                        // inputType: "number",
                        // inputStyle: inputStyle,
                        description: "一鍵解除買賣數量限制",
                    },
                ]
        },
        {
            title: language === "CN" ? "買入稅率" : "Buying Taxes",
            titleStyle: titleStyle,
            style: controlWrapperStyle,
            content:
                [
                    {
                        title: language === "CN" ? "設置購買營銷稅率" : "Set Buy Marketing Fee",
                        style: controlBoxStyle2,
                        inputType: "number",
                        inputStyle: inputStyle,
                        description: "買入稅收進入營銷的百分比",
                    },
                    {
                        title: language === "CN" ? "設置購買回流稅率" : "Set Buy Liquidity Fee",
                        style: controlBoxStyle2,
                        inputType: "number",
                        inputStyle: inputStyle,
                        description: "買入稅收進入回流的百分比",
                    },
                ]
        },
        {
            title: language === "CN" ? "賣出稅率" : "Selling Taxes",
            titleStyle: titleStyle,
            style: controlWrapperStyle,
            content:
                [
                    {
                        title: language === "CN" ? "設置賣出營銷稅率" : "Set Sell Marketing Fee",
                        style: controlBoxStyle2,
                        inputType: "number",
                        inputStyle: inputStyle,
                        description: "賣出稅收進入營銷的百分比",
                    },
                    {
                        title: language === "CN" ? "設置賣出回流稅率" : "Set Sell Liquidity Fee",
                        style: controlBoxStyle2,
                        inputType: "number",
                        inputStyle: inputStyle,
                        description: "賣出稅收進入回流的百分比",
                    },
                ]
        },
        {
            title: language === "CN" ? "白名單地址" : "WhiteList",
            titleStyle: titleStyle,
            // alert: language === "CN" ? "請注意：當所有權放棄後，無法再使用任何開關" : "Note: After you renounce the ownership, you'll never ever be able to control the contract",
            style: controlWrapperStyle,
            content:
                [
                    {
                        inputType: "text",
                        inputStyle: inputStyle,
                        description: "請在上方輸入欲操作之地址"
                    },
                    {
                        title: language === "CN" ? "設置為免稅白名單" : "Set Whitelist",
                        style: controlBoxStyle2,
                        // inputType: "text",
                        // inputStyle: inputStyle,
                        description: "該地址將會變為免稅地址，交易不受買賣稅率影響",
                    },
                    {
                        title: language === "CN" ? "移除免稅白名單" : "Remove From Whitelist",
                        style: controlBoxStyle2,
                        // inputType: "text",
                        // inputStyle: inputStyle,
                        description: "該地址將會從免稅地址移除，其交易受買賣稅率影響",
                    },
                    {
                        title: language === "CN" ? "檢查是否為白名單" : "Check Whitelist",
                        style: controlBoxStyle2,
                        // inputType: "text",
                        // inputStyle: inputStyle,
                        description: "檢查該地址是否為白名單",
                    },
                ]
        },
        {
            title: language === "CN" ? "黑名單地址" : "WhiteList",
            titleStyle: titleStyle,
            style: controlWrapperStyle,
            content:
                [
                    {
                        inputType: "text",
                        inputStyle: inputStyle,
                        description: "請在上方輸入欲操作之地址"
                    },
                    {
                        title: language === "CN" ? "設置黑名單" : "Set Blacklist",
                        style: controlBoxStyle2,
                        // inputType: "text",
                        // inputStyle: inputStyle,
                        description: "該地址將會變為黑名單地址，無法進行轉帳以及賣出代幣",
                    },
                    {
                        title: language === "CN" ? "移除黑名單" : "Remove From Blacklist",
                        style: controlBoxStyle2,
                        // inputType: "text",
                        // inputStyle: inputStyle,
                        description: "該地址將會從黑名單地址移除，交易恢復正常",
                    },
                    {
                        title: language === "CN" ? "檢查是否為黑名單" : "Check Blacklist",
                        style: controlBoxStyle2,
                        // inputType: "text",
                        // inputStyle: inputStyle,
                        description: "檢查該地址是否為黑名單",
                    },
                ]
        },
        {
            title: language === "CN" ? "項目方地址" : "WhiteList",
            titleStyle: titleStyle,
            style: controlWrapperStyle,
            content:
                [
                    {
                        title: language === "CN" ? "更改營銷錢包" : "Set Marketing Wallet",
                        style: controlBoxStyle2,
                        inputType: "text",
                        inputStyle: inputStyle,
                        description: "該地址將會變為營銷錢包，接受營銷稅收以及回流",
                    },
                ]
        },
        {
            title: language === "CN" ? "所有權" : "Ownership",
            titleStyle: titleStyle,
            alert: language === "CN" ? "請注意：當所有權放棄後，無法再使用任何開關" : "Note: After you renounce the ownership, you'll never ever be able to control the contract",
            style: controlWrapperStyle,
            content:
                [
                    {
                        title: language === "CN" ? "放棄所有權" : "Renounce Ownership",
                        style: controlBoxStyle2,
                        description: "代幣所有權將轉移給黑洞地址，且無法再找回",
                    },
                    {
                        title: language === "CN" ? "轉移所有權" : "Transfer Ownership",
                        style: controlBoxStyle2,
                        inputType: "text",
                        inputStyle: inputStyle,
                        description: "代幣所有權將轉移給該地址",
                    },
                ]
        },
    ]

    const handleClick = () => {
        setIsPro(!isPro);
    }

    const handleFunctionClick = (value1, value2) => {
        console.log(value1);
        console.log(value2)
    }

    return (
        //  控制台頁面
        <div style={{
            width: '90%',
            color: 'black',
            position: 'absolute',
            height: '85vh',
            overflowY: 'scroll',
            paddingLeft: '20px',
            border: '1px solid lightgray'
        }}>
            <div style={{ textAlign: 'left', paddingTop: '15px', display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
                <h1>{texts.title}</h1><br />
                <div style={{
                    display: 'flex',
                    flexDirection: 'row',
                    alignItems: 'center',
                    fontSize: '20px',
                    fontWeight: 'bold',
                    justifyContent: 'right',
                }}>
                    <label
                        className="switch">
                        <input type="checkbox" onClick={handleClick} />
                        <div className={`slider ${isPro ? "on" : "off"}`}>
                            <span className={`circle ${isPro ? "right" : "left"}`}></span>
                        </div>
                    </label>
                </div>
            </div>
            {/* <div style={{ textAlign: 'right', width: '100%' }}>
                {
                    language === "CN" ? "專業版開關" : "Pro Switch"
                }
            </div> */}

            <div className='controlWrapper' style={{
                display: 'flex',
                flexDirection: 'column',
                height: '75vh',
                alignItems: 'center',
                // maxWidth: '600px',
                marginLeft: '-2vw',
            }}>
                <div style={{
                    display: 'flex',
                    flexWrap: 'wrap',
                    paddingTop: '10px',
                    width: '90%',
                    // alignItems: 'center',
                    justifyContent: 'space-between',
                }}>
                    {
                        controls.map((control, index) => {
                            return (
                                <div key={index} style={control.style}>
                                    <span style={control.titleStyle}>{control.title}</span>
                                    <span style={{ paddingLeft: '20px', }}>{control.alert}</span>
                                    <div style={{ display: 'flex', flexDirection: 'column' }}>
                                        {
                                            control.content !== undefined &&
                                            control.content.map((content, indexed) => {
                                                return (
                                                    <div key={indexed}>
                                                        <div style={{
                                                            display: 'flex',
                                                            justifyContent: 'center',
                                                            alignItems: 'center',
                                                        }}>
                                                            <div
                                                                style={content.style}
                                                                onClick={() => handleFunctionClick(index, indexed)}
                                                            >
                                                                <span>{content.title}</span>
                                                            </div>
                                                            {
                                                                content.inputType !== undefined &&
                                                                <input
                                                                    text={content.inputType}
                                                                    style={content.inputStyle}
                                                                    placeholder={content.placeholder}
                                                                    onChange={content.function}
                                                                />
                                                            }
                                                        </div>
                                                        {
                                                            !isPro &&
                                                            <p style={{
                                                                color: 'gray',
                                                                marginLeft: '10%',
                                                            }}>{content.description}</p>
                                                        }
                                                    </div>
                                                )
                                            })
                                        }
                                    </div>
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        </div>
    )
}

export default ControlPanel
